.modal {
  top: 10% !important;
  max-height: 70vh !important;
}

@media (min-width: 600px) {
  .modal {
    width: 500px !important;
    left: calc(50% - 250px) !important;
  }
}

.dateRangeWrapper {
  margin: 0 auto;
}

.pickerWrapper,
.dateRangeWrapper {
  width: max-content;
}

.inputsWrapper {
  margin-bottom: 1rem;
}

.pickerWrapper {
  margin-bottom: 2rem;
}

.inputsWrapper {
  display: flex;
}

.dateInputError {
  border-color: red;
  outline: red auto;
}

.hasError {
  background-color: #fff;
  animation-name: toError, backToNormal;
  animation-delay: 0s, 1s;
  animation-duration: 1s, 2s;
}

@keyframes toError {
  to {
    background-color: #fcdcdc;
  }
}
@keyframes backToNormal {
  to {
    background-color: #fff;
  }
}

.pickerError {
  padding-top: 0.5rem;
  text-align: center;
  color: red;
  font-weight: 700;
}

.buttonWrap {
  display: flex;
  justify-content: flex-end;
}

.title {
  font-size: 2rem;
  font-weight: 700;
  line-height: 2;
  margin-bottom: 1rem;
}

.magRangeWrap {
  margin-bottom: 1rem;
}
